import { makeStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'

const useStyles = makeStyles({
  root: {
    display: 'flex',
    justifyContent: 'center',
    margin: '1em auto 0',
    '& > button': {
      margin: '0 .45em'
    }
  }
})

export default function CashbackFilterButtons({ years, activeYear, onClick }) {
  const classes = useStyles()
  const filterByYearButtons = years.map((year, i) => {
    return (
      <Button
        variant={activeYear === year ? 'contained' : 'outlined'}
        color="primary"
        key={i}
        onClick={() => onClick(year)}
      >
        {year}
      </Button>
    )
  })

  return (
    <Grid item xs={12}>
      <div className={classes.root}>
        <Button
          variant={!activeYear ? 'contained' : 'outlined'}
          color="primary"
          onClick={() => onClick('')}
        >
          All
        </Button>
        {filterByYearButtons.reverse()}
      </div>
    </Grid>
  )
}
