/* eslint-disable no-unused-vars */
export default function CashbackGeneralDataExtractor(data, year) {
  const dataByMonths = year
    ? data[year]
    : Object.keys(data)
        .map((year) => data[year])
        .reduce((result, current) => ({
          ...current,
          ...result
        }))

  let totalUsers = 0
  let totalUplUsers = 0
  let totalPoints = 0
  const months = []
  const categories = []

  for (const month in dataByMonths) {
    const dataByCategories = dataByMonths[month].data
    months.push(month)
    categories.push(dataByCategories)
    for (const category in dataByCategories) {
      const [
        cashbackPercentage,
        minProfitMargin,
        categoryUsers,
        salesCount,
        fullPrice,
        selling,
        receivedPoints,
        uplUsers,
        checksCount,
        profitMarginPercentage,
        turnover,
        profitMarginGrivnas,
        responsePercentage,
        receivedPointsPercentage,
        checksCountForOneUplUser,
        averageSellingPrice
      ] = dataByCategories[category]
      totalUsers += categoryUsers
      totalUplUsers += uplUsers
      totalPoints += receivedPoints
    }
  }

  return [totalUsers, totalUplUsers, totalPoints, months, categories]
}
