import React from 'react'
import { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import TableFooter from '@material-ui/core/TableFooter'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Paper from '@material-ui/core/Paper'
import Button from '@material-ui/core/Button'
import NFormatter from 'util/NumbersFormatter'

const useStyles = makeStyles({
  root: {
    marginBottom: '3em'
  },
  table: {
    minWidth: 600
  },
  btnMonth: {
    minWidth: 150,
    whiteSpace: 'nowrap',
    fontSize: 13,
    marginRight: -25
  },
  categoryTable: {
    '& > td:first-child': {
      paddingRight: 0
    }
  },
  category: {
    fontSize: 11,
    maxHeight: 30,
    overflow: 'hidden'
  },
  total: {
    '& > td': {
      fontSize: 14,
      fontWeight: 500,
      color: 'rgba(0, 0, 0, 0.87)'
    }
  }
})

function createRows(categories) {
  const rows = []
  const rowTotal = {
    users: 0,
    selling: 0,
    points: 0
  }
  const createData = (name, users, selling, points) => {
    return { name, users, selling, points }
  }

  for (const category in categories) {
    if (Object.hasOwnProperty.call(categories, category)) {
      const users = categories[category][2]
      const selling = categories[category][5]
      const points = categories[category][6]

      rows.push(createData(category, users, selling, points))
      rowTotal.users += users
      rowTotal.selling += selling
      rowTotal.points += points
    }
  }

  return [rows, rowTotal]
}

const TableLabel = ({ text }) => (
  <small dangerouslySetInnerHTML={{ __html: text }}></small>
)

export default function CashbackDataTable({
  title,
  categories,
  onShowDetails
}) {
  const classes = useStyles()
  const [rows, rowTotal] = createRows(categories)
  const [sortBy, setSortBy] = useState('users')
  const [orderBy, setOrderBy] = useState('desc')

  const labels = {
    users: 'Учасників',
    selling: 'Реалізація,&nbsp;грн.',
    points: 'Отримані&nbsp;бали'
  }

  const makeSort = (value) => {
    setSortBy(value)
    setOrderBy(
      value === sortBy ? (orderBy === 'desc' ? 'asc' : 'desc') : 'desc'
    )
  }

  return (
    <TableContainer component={Paper} className={classes.root}>
      <Table className={classes.table} size="small" aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>
              <Button
                variant="contained"
                color="primary"
                className={classes.btnMonth}
                onClick={() => onShowDetails(title)}
              >
                {title}
              </Button>
            </TableCell>
            {Object.entries(labels).map(([key, label]) => (
              <TableCell align="right" onClick={() => makeSort(key)} key={key}>
                <TableSortLabel
                  active={sortBy === key}
                  direction={sortBy === key ? orderBy : 'asc'}
                >
                  <TableLabel text={label} />
                </TableSortLabel>
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {rows
            .sort((a, b) =>
              orderBy === 'desc' ? b[sortBy] - a[sortBy] : a[sortBy] - b[sortBy]
            )
            .map(({ name, users, selling, points }) => (
              <TableRow key={name} className={classes.categoryTable}>
                <TableCell>
                  <div className={classes.category}>{name}</div>
                </TableCell>
                <TableCell align="right">{NFormatter(users)}</TableCell>
                <TableCell align="right">{NFormatter(selling)}</TableCell>
                <TableCell align="right">{NFormatter(points)}</TableCell>
              </TableRow>
            ))}
        </TableBody>
        <TableFooter>
          <TableRow className={classes.total}>
            <TableCell component="td">Всього</TableCell>
            <TableCell align="right">{NFormatter(rowTotal.users)}</TableCell>
            <TableCell align="right">{NFormatter(rowTotal.selling)}</TableCell>
            <TableCell align="right">{NFormatter(rowTotal.points)}</TableCell>
          </TableRow>
        </TableFooter>
      </Table>
    </TableContainer>
  )
}
