import { useSelector } from 'react-redux'
import { makeStyles } from '@material-ui/core/styles'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import Avatar from '@material-ui/core/Avatar'
import theme from 'theme'
import NumbersFormatter from 'util/NumbersFormatter'
import { useDispatch } from 'react-redux'
import { fetchData } from 'redux/reducer'

const useStyles = makeStyles({
  root: {
    maxWidth: 300,
    width: '100%',
    margin: '0 auto 1em',
    position: 'relative',
    '& .MuiCardHeader-title': {
      fontSize: '1.7em',
      textIndent: '-1em',
      fontWeight: 500
    },
    '& .MuiCardHeader-subheader': {
      position: 'absolute',
      right: '1em',
      top: '50%',
      transform: 'translateY(-50%)',
      color: theme.palette.success.main,
      transition: 'all .3s'
    }
  }
})

export default function InfoCard({ platform, downloads }) {
  const classes = useStyles()
  const dispatch = useDispatch()
  const dataType = useSelector((state) => state.app.dataType)

  return (
    <Card className={classes.root}>
      <CardHeader
        avatar={
          <Avatar
            aria-label="recipe"
            style={{ backgroundColor: theme.palette.platforms[platform] }}
            onClick={() =>
              platform === 'total' && dispatch(fetchData(dataType))
            }
          >
            {theme.icons[platform === 'total' ? dataType : platform]}
          </Avatar>
        }
        title={NumbersFormatter(downloads)}
      />
    </Card>
  )
}
