import { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import CashbackFilterButtons from 'components/cashback/CashbackFilterButtons'
import CashbackInfoCard from 'components/cashback/CashbackInfoCard'
import CashbackDataTable from 'components/cashback/CashbackDataTable'
import CashbackMonthDetails from 'components/cashback/CashbackMonthDetails'
import CashbackGeneralDataExtractor from 'util/CashbackGeneralDataExtractor'
import CashbackDetailsExtractor from 'util/CashbackDetailsExtractor'

const useStyles = makeStyles({
  block: {
    marginTop: '1.5em'
  }
})

export default function CashbackScreen({ data }) {
  const classes = useStyles()
  const [activeYear, setActiveYear] = useState('')
  const years = Object.keys(data)
  const [totalUsers, totalUplUsers, totalPoints, months, categories] =
    CashbackGeneralDataExtractor(data, activeYear)
  const [showDetails, setShowDetails] = useState(false)
  const [detailsMonth, setDetailsMonth] = useState(null)
  const [details, setDetails] = useState(null)
  const handleShowDetails = (month) => {
    setDetailsMonth(month)
    setDetails(CashbackDetailsExtractor(data, month))
    setShowDetails(true)
  }

  return (
    <>
      <CashbackFilterButtons
        years={years}
        activeYear={activeYear}
        onClick={(year) => setActiveYear(year)}
      />

      <Grid item xs={12}>
        <CashbackInfoCard title="учасників" value={totalUsers} />
        <CashbackInfoCard title="УПЛ з покупками" value={totalUplUsers} />
        <CashbackInfoCard title="нараховано балів" value={totalPoints} />
      </Grid>

      <Grid item xs={12}>
        {months.map((month, i) => (
          <CashbackDataTable
            title={month}
            categories={categories[i]}
            onShowDetails={(month) => handleShowDetails(month)}
            key={i}
            className={classes.dataTable}
          />
        ))}
      </Grid>

      {showDetails && (
        <CashbackMonthDetails
          month={detailsMonth}
          details={details}
          onHide={() => setShowDetails(false)}
        />
      )}
    </>
  )
}
