import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import NFormatter from 'util/NumbersFormatter'

const useStyles = makeStyles({
  root: {
    marginBottom: '3em'
  },
  tableTitle: {
    fontSize: 12,
    lineHeight: 1.3
  },
  tableRow: {
    '& > td:first-child': {
      minWidth: 120,
      fontSize: 11,
      paddingRight: 0
    },
    '& > td:not(:first-child)': {
      whiteSpace: 'nowrap'
    }
  }
})

function createTable(data) {
  const values = Object.values(data)
  const cols = values.length
  const rows = values[0].length
  const cells = []

  for (let row = 0; row < rows; row++) {
    const cell = []
    for (let col = 0; col < cols; col++) {
      cell.push(values[col][row])
    }
    cells.push(cell)
  }

  return [Object.keys(data), cells]
}

export default function CashbackDetailsDataTable({ data }) {
  const classes = useStyles()
  const [tHead, tBody] = createTable(data)

  return (
    <TableContainer component={Paper} className={classes.root}>
      <Table size="small">
        <TableHead>
          <TableRow>
            {tHead.map((title, i) => (
              <TableCell align={i === 0 ? 'left' : 'right'} key={title}>
                <div
                  className={classes.tableTitle}
                  dangerouslySetInnerHTML={{ __html: title }}
                ></div>
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {tBody.map((row, i) => (
            <TableRow className={classes.tableRow} key={i}>
              {row.map((value, j) => (
                <TableCell align={j === 0 ? 'left' : 'right'} key={j}>
                  {isNaN(value) ? value : NFormatter(value)}
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}
