import { createSlice } from '@reduxjs/toolkit'
import API from 'util/API'
import InitialDataState, { InitialIncrementsState } from 'util/InitialDataState'
import DataValidator from 'util/DataValidator'
import DataCombiner from 'util/DataCombiner'
import DataFilteredByYears from 'util/DataFilteredByYears'
import { error419Message, errorNetwork } from 'util/messages'

export const slice = createSlice({
  name: 'app',
  initialState: {
    code: '',
    pending: true,
    isLoggedIn: false,
    data: InitialDataState,
    dataByYears: {},
    dataType: 'downloads',
    filterByYears: [],
    increments: InitialIncrementsState,
    lastUpdateDate: null,
    serverError: false,
    serverErrorMessage: ''
  },
  reducers: {
    setCode: (state, { payload }) => {
      state.code = payload
      localStorage.setItem('amp_code', payload)
    },
    setPending: (state, { payload }) => {
      state.pending = payload
    },
    setIsLoggedIn: (state, { payload }) => {
      state.isLoggedIn = payload
    },
    setData: (state, { payload }) => {
      state.dataByYears = payload.data
      if (state.filterByYears.length) {
        state.data = DataCombiner(DataFilteredByYears(state.dataByYears, state.filterByYears))
      } else {
        state.data = DataCombiner(state.dataByYears)
      }
    },
    setCashbackData: (state, { payload }) => {
      state.data = payload.data
      localStorage.setItem('amp_active_screen', 'cashback')
    },
    setDataType: (state, { payload }) => {
      state.dataType = payload
    },
    setIncrements: (state, { payload }) => {
      state.increments = payload
    },
    resetIncrements: (state) => {
      state.increments = InitialIncrementsState
    },
    getDataByYear: (state, { payload }) => {
      const year = payload
      const sorted = (arr) => arr.sort((a, b) => b - a)

      if (state.filterByYears.includes(year)) {
        state.filterByYears = sorted(state.filterByYears.filter((y) => y !== year))
      } else {
        state.filterByYears = sorted([...state.filterByYears, year])
      }

      if (state.filterByYears.length) {
        state.data = DataCombiner(DataFilteredByYears(state.dataByYears, state.filterByYears))
      } else {
        state.data = DataCombiner(state.dataByYears)
      }
    },
    getTotalData(state) {
      state.data = DataCombiner(state.dataByYears)
      state.filterByYears = []
    },
    handleServerError: (state, { payload }) => {
      state.serverError = true
      state.serverErrorMessage = payload

      const formerData = JSON.parse(
        localStorage.getItem(`former_${state.dataType}_data`)
      )
      if (formerData) {
        state.data = formerData
        state.isLoggedIn = true
        state.lastUpdateDate = localStorage.getItem(
          `amp_last_${state.dataType}`
        )
      }
    },
    removeServerError: (state) => {
      state.serverError = false
      state.serverErrorMessage = ''
    }
  }
})

export const {
  setCode,
  setPending,
  setIsLoggedIn,
  setData,
  setCashbackData,
  setIncrements,
  resetIncrements,
  getDataByYear,
  getTotalData,
  setDataType,
  handleServerError,
  removeServerError
} = slice.actions

export default slice.reducer

export function fetchData(type = 'downloads') {
  return async (dispatch) => {
    dispatch(setPending(true))
    dispatch(setDataType(type))
    dispatch(removeServerError())

    try {
      const response = await API.get(`app-${type}`)
      switch (type) {
        case 'cashback':
          dispatch(setCashbackData({ data: response.data }))
          break

        default:
          DataValidator(response.data)
          dispatch(setData({ data: response.data, type }))
          break
      }
      dispatch(setIsLoggedIn(true))
    } catch (error) {
      handleErrorResponse(dispatch, error)
    } finally {
      dispatch(setPending(false))
    }
  }
}

function handleErrorResponse(dispatch, error) {
  if (error.response && error.response.status === 403) {
    dispatch(setIsLoggedIn(false))
  } else {
    let message = error.message
    if (message.includes('429')) message = error419Message
    if (message.includes('Network Error')) message = errorNetwork
    dispatch(handleServerError(message))
  }
}
