import React from 'react'
import theme from 'theme'
import Card from '@material-ui/core/Card'
import { makeStyles } from '@material-ui/core/styles'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import { useSelector, useDispatch } from 'react-redux'
import { fetchData } from 'redux/reducer'

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
    // maxWidth: 300,
    margin: 'auto auto 1em'
  },
  tab: {
    minHeight: 50,
    '& .MuiTab-wrapper': {
      flexDirection: 'row',
      alignItems: 'flex-start'
    }
  },
  tabCondensed: {
    fontSize: 12
  }
})

export default function Navigation() {
  const classes = useStyles()
  const tabs = ['downloads', 'active', 'sales', 'cashback']
  const dataType = useSelector((state) => state.app.dataType)
  const dispatch = useDispatch()

  const handleChange = (event, type) => {
    dispatch(fetchData(type))
  }

  return (
    <Card className={classes.root}>
      <Tabs
        value={dataType}
        onChange={handleChange}
        variant="fullWidth"
        indicatorColor="primary"
        textColor="primary"
        aria-label="navigation"
      >
        {tabs.map((tab, i) => (
          <Tab
            icon={theme.icons[tab]}
            value={tab}
            label={tab}
            key={i}
            className={classes.tabCondensed}
          />
        ))}
      </Tabs>
    </Card>
  )
}
