import React from 'react'
import DownloadsScreen from 'components/screens/DownloadsScreen'
import ActivityScreen from 'components/screens/ActivityScreen'
import SalesScreen from 'components/screens/SalesScreen'
import CashbackScreen from 'components/screens/CashbackScreen'

const screens = {
  downloads: DownloadsScreen,
  active: ActivityScreen,
  sales: SalesScreen,
  cashback: CashbackScreen
}

export default function ScreenComponent({ type, data }) {
  const Screen = screens[type]

  return <Screen data={data} />
}
