import { adjustRoundPercentage } from 'util/RoundFloatNumbers'

/* eslint-disable no-unused-vars */
export default function CashbackDetailsExtractor(data, month) {
  const dataByMonths = Object.keys(data)
    .map((year) => data[year])
    .reduce((result, current) => ({
      ...current,
      ...result
    }))

  const dataByCategories = dataByMonths[month].data
  const tableData = {
    responsePercentage: {
      titles: [
        'категорія',
        'к-сть учасників, що обрали категорію',
        'к-сть УПЛ з покупками в періоді',
        '% відгуку'
      ],
      values: []
    },
    pointsPercentage: {
      titles: ['категорія', '% факт. нарахування балів'],
      values: []
    },
    cashbackPercentage: {
      titles: [
        'категорія',
        'сума реалізації, грн',
        'сума нарахованого кешбеку',
        '% кешбеку від повного оберту'
      ],
      values: []
    },
    marginPercentage: {
      titles: ['категорія', 'маржа, грн', 'маржа, %'],
      values: []
    },
    averageSellingPrice: {
      titles: ['категорія', 'сер. ціна реалізації товару з категорії, грн'],
      values: []
    }
  }

  for (const category in dataByCategories) {
    const [
      cashbackPercentage,
      minProfitMargin,
      categoryUsers,
      salesCount,
      fullPrice,
      selling,
      receivedPoints,
      uplUsers,
      checksCount,
      profitMarginPercentage,
      turnover,
      profitMarginGrivnas,
      responsePercentage,
      receivedPointsPercentage,
      checksCountForOneUplUser,
      averageSellingPrice
    ] = dataByCategories[category]

    tableData.cashbackPercentage.values.push([
      category,
      selling,
      receivedPoints,
      adjustRoundPercentage(receivedPoints / (selling + receivedPoints), -2)
    ])

    tableData.pointsPercentage.values.push([category, receivedPointsPercentage])

    tableData.marginPercentage.values.push([
      category,
      profitMarginGrivnas,
      profitMarginPercentage
    ])

    tableData.responsePercentage.values.push([
      category,
      categoryUsers,
      uplUsers,
      responsePercentage
    ])

    tableData.averageSellingPrice.values.push([category, averageSellingPrice])
  }

  const details = {}
  const tables = Object.keys(tableData)

  Object.values(tableData).forEach(({ titles, values }, tableIndex) => {
    const table = {}
    titles.forEach((title, titleIndex) => {
      table[title] = []
      values.forEach((value) => table[title].push(value[titleIndex]))
    })
    details[tables[tableIndex]] = table
  })

  if (
    'details' in dataByMonths[month] &&
    dataByMonths[month].details !== null
  ) {
    return { ...dataByMonths[month].details, ...details }
  }

  return details
}
