import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Button from '@material-ui/core/Button'
import CashbackDetailsDataTable from 'components/cashback/CashbackDetailsDataTable'

const useStyles = makeStyles({
  root: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100vw',
    height: '100vh',
    background: '#fafafa',
    paddingBottom: 80,
    zIndex: 9999,
    overflow: 'scroll'
  },
  btnHide: {
    position: 'fixed',
    bottom: 0,
    display: 'block',
    width: '100%',
    borderRadius: 0,
    fontSize: 20,
    padding: 12
  },
  detailsWrapper: {
    height: '100%'
  }
})

export default function CashbackMonthDetails({ month, details, onHide }) {
  const classes = useStyles()

  return (
    <Card className={classes.root}>
      <h2>{month}</h2>
      <Button
        onClick={onHide}
        className={classes.btnHide}
        variant="contained"
        color="primary"
      >
        ЗАКРИТИ
      </Button>
      <CardContent>
        <div className={classes.detailsWrapper}>
          {Object.values(details).map((data, i) => (
            <CashbackDetailsDataTable data={data} key={i} />
          ))}
        </div>
      </CardContent>
    </Card>
  )
}
